import React, { Suspense } from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { CircularProgress, AppBar, Toolbar, IconButton, Typography } from '@material-ui/core'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'
import { Menu as MenuIcon } from '@material-ui/icons'
import { ExampleComponent } from 'react-sundial'

const theme = createMuiTheme({
	palette: {
		primary: {
			main: '#90caf9',
		},
		secondary: {
			main: '#ff7043',
		},
	},
})

const homePath = '/react-sundial'

const App = () => (
	<ThemeProvider theme={theme}>
		<Router>
			<AppBar position="fixed">
				<Toolbar>
					<IconButton aria-label="menu" color="inherit" edge="start">
						<MenuIcon />
					</IconButton>
					<Typography>react-sundial</Typography>
				</Toolbar>
			</AppBar>
			<Suspense fallback={<CircularProgress />}>
				<Switch>
					<Route exact path={homePath}>
						<div>Home Page</div>
					</Route>
					<Route path={`${homePath}/components`}>
						<ExampleComponent text="Create React Library Example ðŸ˜„" />
					</Route>
				</Switch>
			</Suspense>
		</Router>
	</ThemeProvider>
)

export default App
